































import { Component, Prop, Vue, Emit, Watch} from 'vue-property-decorator';
import QuestionPanel from '@/components/QuestionPanel.vue';


@Component({
  components: {
    QuestionPanel,
  },
})
export default class Questions extends Vue {

  // FIXME boolean の変数を作る
  private tr: boolean = true;
  private fal: boolean = false;

  private curPageId: number = 1;
  private changePageId(id: number): void {
    this.curPageId = id;
  }

  private user: string = '';
  // FIXME any
  private questions = [];

  private created(): void {
    //this.getQuestions();
    this.getQuestionsWithPage();
  }
  
  // 質問をページ取得する
  private getQuestionsWithPage(): void {
    const url = 'api/questions/' + String(this.curPageId);
    const headers = {'Authorization': `Bearer ${this.getToken()}`};

    fetch(url, {headers}).then(response => {
      if(response.ok) {
        return response.json();
      }
      return []
    }).then(json => {
      this.questions = [];
      this.questions = json;
    })
  }
  
  // 質問を全取得する
  private getQuestions(): void {
    const url = 'api/questions';
    const headers = {'Authorization': `Bearer ${this.getToken()}`};

    fetch(url, {headers}).then(response => {
      if(response.ok) {
        return response.json();
      }
      return []
    }).then(json => {
      this.questions = json;
    })
  }
  private getToken(): any {
    return localStorage.getItem('token');
  }

  // ページが変われば, 質問を取得し直す
  @Watch('curPageId')
  private pageChanged(): void {
    this.getQuestionsWithPage();
  }
}
